@import url(https://fonts.googleapis.com/css2?family=David+Libre:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Inconsolata:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#quote-box{margin:auto;margin-top:10%;width:60%;height:100%;align-self:center;border-radius:10px;padding:20px 25px 15px 25px;display:flex;flex-flow:column nowrap;align-items:flex-end;background-color:rgba(245,214,39,0.5)}.twitter-share-button{align-items:flex-start;width:41px;margin-right:90%;margin-bottom:3%}#twitter-img{width:40px;opacity:0.8;align-items:flex-start}#text,#author{font-family:'David Libre', 'serif';font-weight:normal}#author{text-align:right}.button{font-family:'Inconsolata', 'Helvetica', 'Arial',         sans-serif;font-size:inherit;text-align:center;font-weight:bold;letter-spacing:3px;text-transform:uppercase;text-decoration:none;border:4px solid rgba(245,214,39,0.5);color:rgba(94,82,13,0.5);background:#fff;outline:none;display:inline-block;position:relative;padding:15px 10px 14px;cursor:pointer;width:auto}.button:after{position:absolute;width:100%;height:100%;border:2px solid #3C14D0;background-color:#3C14D0;left:4px;top:4px;z-index:-1;content:'';-webkit-transition:all 0.5s;-moz-transition:all 0.5s;-o-transition:all 0.5s}.button:hover{top:2px;left:2px}.button:hover:after{top:-2px;left:-2px}

